  .skills-bar-container li .bar-container {
	background: white;
	position: relative;
	width: 100%;
	height: 5px;
	margin-top: 5px;
	display: block;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
