  /* Common style */
  .grid {
	  margin-top: 58px;
  }
  .grid figure {
	  position: relative;
	  z-index: 1;
	  overflow: hidden;
	  width: 22vw;
	  height: 286px;
	  background: #000000;
	  text-align: center;
	  cursor: pointer;
	  border: 14px solid white;
  }
  
  .grid figure img {
	  position: relative;
	  display: block;
	  min-height: 100%;
	  opacity: 0.8;
  }
  
  .grid figure figcaption {
	  padding: 2em;
	  color: #fff;
	  text-transform: uppercase;
	  font-size: 1.25em;
	  -webkit-backface-visibility: hidden;
	  backface-visibility: hidden;
  }
  
  .grid figure figcaption::before,
  .grid figure figcaption::after {
	  pointer-events: none;
  }
  
  .grid figure figcaption,
  .grid figure a {
	  position: absolute;
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 100%;
  }
  
  /* Anchor will cover the whole item by default */
  /* For some effects it will show as a button */
  .grid figure a {
	  z-index: 1000;
	  text-indent: 200%;
	  white-space: nowrap;
	  font-size: 0;
	  opacity: 0;
  }
  
  .grid figure h2 {
	  word-spacing: -0.15em;
	  
	  font-weight: 300;
  }
  
  .grid figure h2 span {
	  font-weight: 800;
  }
  
  .grid figure h2,
  .grid figure p {
	  margin: 0;
  }
  
  .grid figure p {
	  letter-spacing: 1px;
	  font-size: 70%;
  }
  @media screen and (max-width: 69.5em) {
	  .grid figure {
		  width: 50%;
	  }	
  
	  .grid figure figcaption {
		  font-size: 90%;
	  }
  }
  
  
  @media screen and (max-width: 41.5em) {
  
	  .grid figure {
		  width: 100%;
	  }
  }
